import React from 'react';
import PropTypes from 'prop-types';
import defaultTo from 'lodash/defaultTo';
import ProfileStatsBlock from '../../components/ProfileStatsBlock';
import { Path } from '../../routes';

const prepareValue = (value, postfix = '') =>
  `${defaultTo(value, 0)} ${postfix}`;

function ProfilePageStats({ profiles, views, consumption }) {
  return (
    <div className="profile-page__stats">
      <ProfileStatsBlock
        title=""
        items={[
          {
            label: ' ',
            link: Path.ADD,
          },
          {
            label: ' ',
            link: Path.PROFILES,
          },
        ]}
      />
      <ProfileStatsBlock
        title=""
        items={[
          {
            label: ':',
            value: prepareValue(profiles.count, ''),
          },
          {
            label: ':',
            value: prepareValue(profiles.active, ''),
          },
          {
            label: ':',
            value: prepareValue(profiles.inactive, ''),
          },
        ]}
      />
      <ProfileStatsBlock
        title=""
        items={[
          {
            label: ':',
            value: prepareValue(views.today),
          },
          {
            label: ':',
            value: prepareValue(views.yesterday),
          },
          {
            label: '  :',
            value: prepareValue(views.all),
          },
        ]}
      />
      <ProfileStatsBlock
        title=""
        items={[
          {
            value: prepareValue(consumption.forDay, './'),
          },
          {
            value: prepareValue(consumption.forWeek, './'),
          },
          {
            value: prepareValue(consumption.forMonth, './'),
          },
        ]}
      />
    </div>
  );
}

ProfilePageStats.propTypes = {
  profiles: PropTypes.object,
  views: PropTypes.object,
  consumption: PropTypes.object,
};

ProfilePageStats.defaultProps = {
  profiles: {},
  views: {},
  consumption: {},
};

export default ProfilePageStats;
